.burbuja{

  border-radius: 50%;
  background: rgb(192, 192, 192);
  opacity: .3;

  position: absolute;
  bottom: -150;

  animation: burbujas 3s linear infinite;
}
.burbuja:nth-child(1){
  width: 80px;
  height: 80px;
  left: 5%;
  animation-duration: 4s;
  animation-delay: 4s;
}
.burbuja:nth-child(2){
  width: 120px;
  height: 120px;
  left: 35%;
  animation-duration: 4s;
  animation-delay: 5s;
}
.burbuja:nth-child(3){
  width: 50px;
  height: 50px;
  left: 20%;
  animation-duration: 3s;
  animation-delay: 3s;
}
.burbuja:nth-child(4){
  width: 100px;
  height: 100px;
  left: 70%;
  animation-duration: 6s;
  animation-delay: 7s;
}
.burbuja:nth-child(5){
  width: 30px;
  height: 30px;
  left: 60%;
  animation-duration: 4s;
  animation-delay: 5s;
}

@keyframes burbujas {
  0%{
    bottom:0%;
    opacity: 0;
  }
  30%{
    transform: translateX(50px);
  }
  50%{
    opacity: .4;

  }
  100%{
    bottom: 100vh;
    opacity: 0;
  }
}

@media screen and (max-width: 425px) {
  @keyframes burbujas {
    0%{
      bottom:0%;
      opacity: 0;
    }
    30%{
      transform: translateX(15px);
    }
    50%{
      opacity: .4;
  
    }
    100%{
      bottom: 100vh;
      opacity: 0;
    }
  }
}
@media screen and (width: 320px) {
  @keyframes burbujas {
    0%{
      bottom:0%;
      opacity: 0;
    }
    30%{
      transform: translateX(5px);
    }
    50%{
      opacity: .4;
  
    }
    100%{
      bottom: 100vh;
      opacity: 0;
    }
  }
}